.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.loader {
  color: #000;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 48px;
  font-weight: bold;
  display: inline-block;
  position: relative;
}

.loader:before {
  content: "";
  width: 30px;
  height: 30px;
  box-sizing: border-box;
  border: 5px solid #000;
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  animation: rotation .6s linear infinite;
  position: absolute;
  bottom: 8px;
  left: 34px;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.picture-container {
  height: 300px;
  text-align: center;
  border-radius: 10px;
  flex: 1;
  margin: 0 10px;
  padding: 20px;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

h1, p {
  text-align: left;
}

img {
